const extractVersionFromPathname = pathname => {
  const routePrefix = pathname.split("/")[1]
  const deployBranches = JSON.parse(process.env.GATSBY_DEPLOY_BRANCHES)

  if (deployBranches.some(deployBranch => deployBranch.name === routePrefix)) {
    return routePrefix
  } else {
    return ""
  }
}

export default extractVersionFromPathname
