import extractVersionFromPathname from "./extractVersionFromPathname"

export const isHashRoute = url => {
  return url?.startsWith("#")
}

export const isExternalLink = (url, location) => {
  try {
    return location.origin !== new URL(url, location.origin).origin
  } catch (e) {
    return false
  }
}

export const urlRewrite = (url, location) => {
  if (url == null) {
    console.warn(`Null url in ${location.pathname}`)
    return { url: "", isExternalLink: false }
  }

  if (isHashRoute(url)) {
    return { url: url.replace(/\.md/, ""), isExternalLink: false }
  }

  if (isExternalLink(url, location)) {
    return { url: url, isExternalLink: true }
  }

  if (url.startsWith("/")) {
    const version = extractVersionFromPathname(location.pathname)
    return { url: `/${version}${url}`, isExternalLink: false }
  }

  const parentRoute = location.pathname.replace(/\/[^/]*(\/)?$/, "")

  const rewrittenInternamLink = `/${parentRoute}/${url.replace(
    /\.md/,
    ""
  )}`.replace(/\/+/g, "/")

  return { url: rewrittenInternamLink, isExternalLink: false }
}
